.App {
  text-align: center;
  height: 100%;
}

/* .App-logo {
  height: 40vmin;
  pointer-events: none;
} */
#jnk-logo {
  font-size: 1.0em;
}

.jnk-naveText {
  font-size: 0.8em;
}

.jnk-container{
  margin-top: 20px;
}
.fade.modal.show{
  background-color: #343a40;
}

.fade.modal.show  .modal-dialog{
  height: 100%;
}
.fade.modal.show  .modal-content{
  min-height: 90%;
}

/* 네브바 바디 전체영역으로 블랙 주려고 잡은 영역 */
/* .jnk_navBack {
  background-color: #343a40;
} */

.jnk_fixedTop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 56px;
  box-sizing: border-box;
  /* visibility: hidden; */
  z-index: 100;
}

.jnk-menu{
  display: none;
  font-size: 0.8em;
}
.jnk_floatLeft {
  float: left;
  width: 100%;
}
.jnk_wrap {
  position: fixed;
  top: 120px; 
  right: 0;
  left: 0;
}
.jnk_wrap::after{
  display: block;
  content: "";
  clear: both;
}



.jnk-loginCard{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* 사진 더보기 화면 - 이미지 이동 버튼 */
.jnk-imageMoreLeft{
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-shadow: -5px 0px 7px black;
}
.jnk-imageMoreRight{
  position: absolute;
  top: 50%;
  right: -6px;
  transform: translate(-50%, -50%);
  color: white;
  text-shadow: 5px 0px 7px black;
}

.jnk-imageMoreLeft.disabled{
  color : #ffffff85;
  text-shadow: -5px 0px 7px #9d9b9b;
}

.jnk-imageMoreRight.disabled{
  color : #ffffff85;
  text-shadow: 5px 0px 7px #9d9b9b;
}
/* ***************************** */

@media (min-width:  767px) {
  .jnk-menu{
    display: inline;
  }
  #jnk-logo {
    font-size: 1.1em;
  }
  .jnk-naveText {
    font-size: 0.9em;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
